.card[data-card-height="cover-full"],
.card[data-card-height="cover"] {
  height: 100vh;
}

.card[data-card-height="cover-card"] {
  height: calc(100vh - 200px);
}

#form1a-select {
  border-color: rgba(255, 255, 255, 0.1) !important;
  font-size: 15px;
  color: #fff !important;
}

.custom_play_btn {
  border: 0;
  width: 100px;
  border-radius: 4px;
  padding: 3px 5px;
  margin: 8px 7px 12px 7px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  box-shadow: 0 1px 3px rgb(0, 0, 0, 0.5);
  background: #10396A !important;
  border: 2px solid #E6960C;
}

.balance_wrapper {
  font-size: 13px;
  font-family: inherit;
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
}

.balance_wrapper .links {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  text-align: center;
  margin: 0px;
  width: 30%;
  padding-right: 1px solid;
}

.balance_wrapper .pbalance {
  width: 70%;
}

.home_balance {
  overflow: hidden;
}

.balance_btns a {
  text-align: center;
  font-size: 11px;
  padding: 5px;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  color: black;
}

.balance_btns a .main_rounded {
  width: 45px;
  height: 45px;
  padding: 10px;
  display: block;
  margin: 0 auto 6px;
  background: #e9e6f6;
  overflow: hidden;
  -webkit-box-shadow: 0 5px 5px rgba(0, 0, 0, .3);
  box-shadow: 0 5px 5px rgba(0, 0, 0, .3);
  -webkit-border-radius: 50%;
  border-radius: 50%;
}

.links a {
  background: transparent;
  border: none;
  position: relative;
  width: auto;
}

.links a img {
  position: relative;
  width: 80%;
  height: auto;
}

.links a span {
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  font-size: 12px;
  text-align: center;
  color: #223651;
}

.pbalance {
  overflow: hidden;


}

.balance_btns {
  padding-bottom: 5px;
  display: flex;
  width: 100%;
  justify-content: space-around;
  border-left: 1px solid #d7d7d7;
}

.balance_btns .btns {
  text-align: center;
  width: 100%;
  margin-top: 5px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.balance_btns .btns img {
  margin-right: 5px;
}

.balance_btns .btns i {
  margin: 4px -23px;
  position: absolute;
  font-size: 18px;
}

.page-title .logo {
  height: 46px;
  width: 100%;
  margin: 5px 10px;
}

.currency_icon img {
  height: 27px;
  margin: 10px 0 0 10px!important;
}

.btn_green {
  color: #212529 !important;
  background-color: white;
  border-radius: 4px !important;
  border: 0;
  box-shadow: 0 1px 3px rgb(0, 0, 0, 0.5);
  font-size: 15px !important;
  line-height: 20px !important;
  padding: 8px 0;
  font-weight: bold;
  text-transform: uppercase;
}

.register_btn {
  width: 150px;
  background: #E6960C;
  border-color: #E6960C;
  border-radius: 4px;
}

.badge_anima {
  background: #f38120;
  margin: 3px -40px;
  position: absolute !important;
  color: #fff !important;
  line-height: 20px;
  width: 20px;
  text-align: center;
  border-radius: 10px;
  top: 5px;
  right: 50%;
  font-weight: 700 !important;
  font-size: 12px !important;
  opacity: 1 !important;
}

.main_pulse {
  animation: pulse 1s infinite ease-in-out alternate;
}

