#settings {
  padding: 10px;
  font-size: 18px !important;
  font-weight: 700 !important;

}

.setting_head {
  color: #D4A948 !important;
  border-bottom: 2px solid;
  padding-bottom: 10px !important;
}

#settings a {
  padding: 15px 0 !important;
  border-bottom: 1px solid;
  color: #7e41b6;
  display: block;
  font-size: 15px;
}

.color_yellow {
  color: #D4A948 !important;
}

#deposit_cards {
  padding: 15px;
  overflow: hidden;
}

#deposit_cards .pay_menu {
  background: transparent;
  display: block;
  padding: 7px;
  border-radius: 4px;
}

#deposit_cards .pay_menu img {
  width: 100%;
}

.custom-icon {
  display: flex;
  width: 65%;
  align-items: center;
  justify-content: space-between;
}

.custom-icon i {
  font-size: 22px;
  color: gray;
  margin-right: 10px;
}

