.announcement {
  padding: 10px;
  font-size: 12px;
  color: #213034;
  display: flex;
  background: #efedfd;
  border-bottom: 1px solid #d9d0f5;
  align-items: center;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
}

.announcement>div {
  display: flex;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
}

.announcement img {
  width: 18px;
  height: 18px;
  background-repeat: no-repeat;
  -webkit-background-size: 18px;
  -o-background-size: 18px;
  background-size: 18px;
  display: block;
  margin-right: 5px;
}

.announcement .running_text {
  padding: 0 0 0 5px;
  width: 220px;
  color: #55289a;
}

.custom_title {
  background-image: url(./images/sidepush-header1.jpg);
  width: 230px;
  height: 184px;
  background-position: top center;
  background-repeat: no-repeat;
  -webkit-background-size: 280px 184px;
  -o-background-size: 280px 184px;
  background-size: 230px 155px;
}
.custom_title p{
  font-size: 11px !important;
  font-weight: 400 !important;
}
.custom_title div{
  padding: 39px 0 10px;
  margin-left: -7px;
}
.custom_title button{
  padding: 30px 0 10px;
  border: none;
  outline: none;
  background: transparent;
}
.btn_gold{
  padding: 10px 0;
  background-image: url(./images/btn-login.png);
  -webkit-background-size: 156px 50px;
  -o-background-size: 156px 50px;
  background-size: 156px 50px;
  width: 156px;
  height: 50px;
  display: block;
  text-align: center;
  color: inherit;
  margin: 5px auto;
  font-weight:bold;
}